/* Timeline.css */

.timeline {
  padding: 40px 0;
  background-color: #f8f8f8;
  /* Choose a background color for the timeline */
}

.timeline-content {
  max-width: 90%;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.timeline-card {
  width: 30%;
  /* Adjust the width as needed */
  margin: 10px;
  margin-bottom: 40px;
  background-color: #fff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  overflow: hidden;
  box-sizing: border-box;
}

.card-date {
  width: 100%;
  background-color: #ff85a2;
  /* Choose a color for the date box */
  color: #fff;
  padding: 15px;
  text-align: center;
  font-weight: bold;
}

.card-content {
  padding: 20px;
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 10px;
}

p {
  font-size: 1rem;
}

@media screen and (max-width: 767px) {
  .timeline-card {
    width: calc(50% - 20px);
    /* Adjust the width for medium-sized screens */
  }
}

@media screen and (max-width: 480px) {
  .timeline-card {
    width: calc(100% - 20px);
    /* Adjust the width for small screens (e.g., smartphones) */
  }
}